footer {
    border-top: 1px solid black;
    margin-top: 3rem;
    padding: 2rem 0;
    justify-content: space-between;
    align-items: center;
    display: flex;

}

footer p {
    font-weight: 500;
    max-width: 600px;
    margin-right: 0.5rem;
}

@media(max-width:576px) {
    footer p {
        font-size: 0.8rem;
    }
}