.btnCartCount {
    width: 50px;
    height: 50px;
    background-color: black;
    color:white;
    border-radius: 15px;
    transform: rotate(45deg);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 1.5rem;
    cursor: pointer;
    right: 1rem;
}

.btnCartCount i {
    transform: rotate(-45deg);
}

.btnCartCount .count {
    position: absolute;
    background-color: red;
    top: -8px;
    left: -9px;
    transform: rotate(-45deg);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    font-size: 0.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;

}