header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 0.5rem;
    height: 100vh;
}

.header-img {
    width: 100%;
    align-self: center;
    border-radius: 50%;
}

.header-content {
   display: flex;
   flex-direction: column; 
}

.content-main {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
}

.content-main h1 {
    font-size: 3rem;
    margin-bottom: 1.5rem;
}

.content-main button {
    background-color: #000;
    color: white;
    width: 200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1.5rem;
    outline: none;
    border: none;
    font-size: 1rem;
    border-radius: 25px;
    cursor: pointer;
    margin-top: 2.5rem;
}

.content-main p {
    font-weight: 500;
}

@media(max-width: 768px) {
    .content-main h1 {
        font-size: 1.2rem;
        margin-bottom: 1rem;
    }

    .content-main p {
        font-size: 0.8 rem;
    }

    .content main button {
        font-size: 0.8rem;
        width: 150px;
        padding: 0.5rem 1rem;
        margin-top: 1.5rem;
    }

}